<template>
	<view class="all">
		<div class="close df aic jce">
			<image class="img" src="../../static/fonts/Close.svg" mode=""></image>
		</div>
		<div class="welcome fz20 md16 mt20">
			选择您的身份证件的签发国家/地区
		</div>
		<div @click="openPopup" class="country df aic jcsb">
			<div class="box df aic jcc">
				<image class="img" src="../../static/fonts/hk.svg" mode=""></image>
				<p class="fz16 ml8 df aic jcc">香港</p>
			</div>
			<div class="right">
				<u-icon name="arrow-right" color="#12d18e"></u-icon>
			</div>
		</div>
		<div class="welcome fz20 md16 mt20">
			选择您的文件类型
		</div>
		<!-- 循环渲染 -->
		<div @click="photograph" v-for="item in data" :key="item.icon" class="country mb10 df aic jcsb">
			<div class="box df aic jcc">
				<image class="img" src="../../static/fonts/ID.svg" mode=""></image>
				<p class="fz16 ml8 df aic jcc">{{item.name}}</p>
			</div>
			<div class="right">
				<u-icon name="arrow-right" color="#12d18e"></u-icon>
			</div>
		</div>
		<button @click="goUpload">点击相机拍照，上传图片</button>
		<P class="fz12 gary df aic jcc mt64">如果找不到您的文件类型或签发国家，请联系<span class="sign">客户支持</span>。</P>
		<p class="fz12 nineD df aic jcc mt16">由 Sumsub 提供支持</p>
		<!-- 显示弹窗 -->
		<u-popup :show="show" :round="10" mode="bottom" @close="close" @open="open">
			<view>
				<u-search :animation="true" :clearabled="true" placeholder="请输入搜索关键字"></u-search>
				<div class="hongkong df aic">
					<image class="img" src="../../static/fonts/hk.svg" mode=""></image>
					<span class="ml8 df aic">香港</span>
				</div>
				<div class="hongkong df aic">
					<image class="img" src="../../static/fonts/hk.svg" mode=""></image>
					<span class="ml8 df aic">香港</span>
				</div>
				<div class="hongkong df aic">
					<image class="img" src="../../static/fonts/hk.svg" mode=""></image>
					<span class="ml8 df aic">香港</span>
				</div>
				<div class="hongkong df aic">
					<image class="img" src="../../static/fonts/hk.svg" mode=""></image>
					<span class="ml8 df aic">香港</span>
				</div>
				<div class="hongkong df aic">
					<image class="img" src="../../static/fonts/hk.svg" mode=""></image>
					<span class="ml8 df aic">香港</span>
				</div>
				<div class="hongkong df aic">
					<image class="img" src="../../static/fonts/hk.svg" mode=""></image>
					<span class="ml8 df aic">香港</span>
				</div>
			</view>
		</u-popup>
		<!-- <u-button @click="show = true">打开</u-button> -->
	</view>
</template>

<script>
	export default {
		data() {
			return {
				data: [{
						icon: 'passport',
						name: '护照'
					},
					{
						icon: 'drivelicense',
						name: '驾照'
					},
					{
						icon: 'ID',
						name: '身份证'
					}, {
						icon: 'Household',
						name: '户口本/居住许可证'
					}
				],
				// 弹窗数据
				show: false
			};
		},
		methods: {
			open() {
				// console.log('open');
			},
			close() {
				this.show = false
				// console.log('close');
			},
			openPopup() {
				this.show = true
				this.open()
			},
			photograph() {
				// 点击打开摄像头拍照，或者从相册中选取
				uni.chooseImage({
					count: 6, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album', 'camera'], //album 从相册选图，camera 使用相机
					success: function(res) {
						console.log(JSON.stringify(res.tempFilePaths)); //拍照图片的路径
					}
				});
			},
			goUpload() {
				uni.navigateTo({
					url: '/pages/Upload/Upload'
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.country {
		width: 686rpx;
		height: 112rpx;
		border-radius: 32rpx;
		padding: 0 48rpx;
	}

	.img {
		width: 48rpx;
		height: 48rpx;
		border-radius: 50%;
	}

	.hongkong {
		padding: 40rpx 20rpx;
	}
</style>